import React from 'react';
import {Panel, PanelHeader, Group, Button, Div, FixedLayout, Input, List, CellButton} from '@vkontakte/vkui';
import { connect } from 'react-redux';
import * as actions from '../Store/actions';
import '../style.css';
import onRight from '../img/onRight.png';
import onLeft from '../img/onLeft.png';
//import Adventure from '../fonts/Adventure_2.ttf';
//import Markella_0 from '../fonts/Markella_0.ttf';
//import BetinaScriptC_Bold_2 from '../fonts/BetinaScriptC-Bold_2.otf';

class FontSelection extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			textValue: "",
		}
		this.go = this.props.go.bind(this);
	}
	handleImput = (e) => {
		this.setState({
			textValue: e.target.value
		})
	}

	render() {
		return (
			<Panel id={this.props.id}>
				<PanelHeader>
					 <strong></strong>
				</PanelHeader>
					<Group className="Group">
						<List>
							<Input
								top="" 
								placeholder=" "
								alignment="center"
								onChange={this.handleImput}
							/>
						</List>	
						<CellButton 
							style={{fontFamily: "Adventure Regular",
									backgroundColor: "white"}}
							onClick={this.go}
							data-to="textColorSelection">
							1 {this.state.textValue}
						</CellButton>
						<CellButton 
							style={{fontFamily: "Raleway Light",
									backgroundColor: "white"}}
							onClick={this.go}
							data-to="textColorSelection">
							1 {this.state.textValue}
						</CellButton>
						<CellButton 
							style={{fontFamily: "Rosa Marena Regular",
									backgroundColor: "white"}}
							onClick={this.go}
							data-to="textColorSelection">
							1 {this.state.textValue}
						</CellButton>
						<CellButton 
							style={{fontFamily: "Segoe Print",
									backgroundColor: "white"}}
							onClick={this.go}
							data-to="textColorSelection">
							1 {this.state.textValue}
						</CellButton>
						<CellButton 
							style={{fontFamily: "Still Time Cyr Regular",
									backgroundColor: "white"}}
							onClick={this.go}
							data-to="textColorSelection">
							1 {this.state.textValue}
						</CellButton>
						<CellButton 
							style={{fontFamily: "StudioScriptC Regular",
									backgroundColor: "white"}}
							onClick={this.go}
							data-to="textColorSelection">
							1 {this.state.textValue}
						</CellButton>
						<CellButton 
							style={{fontFamily: "AllegroScript",
									backgroundColor: "white"}}
							onClick={this.go}
							data-to="textColorSelection">
							1 {this.state.textValue}
						</CellButton>
						<CellButton 
							style={{fontFamily: "Annabelle Regular",
									backgroundColor: "white"}}
							onClick={this.go}
							data-to="textColorSelection">
							1 {this.state.textValue}
						</CellButton>
						<CellButton 
							style={{fontFamily: "Aquarelle Regular",
									backgroundColor: "white"}}
							onClick={this.go}
							data-to="textColorSelection">
							1 {this.state.textValue}
						</CellButton>
						<CellButton 
							style={{fontFamily: "ArtScript Regular",
									backgroundColor: "white"}}
							onClick={this.go}
							data-to="textColorSelection">
							1 {this.state.textValue}
						</CellButton>
						<CellButton 
							style={{fontFamily: "Aurora Script Regular",
									backgroundColor: "white"}}
							onClick={this.go}
							data-to="textColorSelection">
							1 {this.state.textValue}
						</CellButton>
						<CellButton 
							style={{fontFamily: "Bello Pro",
									backgroundColor: "white"}}
							onClick={this.go}
							data-to="textColorSelection">
							1 {this.state.textValue}
						</CellButton>
						<CellButton 
							style={{fontFamily: "BetinaScriptC",
									backgroundColor: "white"}}
							onClick={this.go}
							data-to="textColorSelection">
							1 {this.state.textValue}
						</CellButton>
						<CellButton 
							style={{fontFamily: "Cansellarist Cansellarist",
									backgroundColor: "white"}}
							onClick={this.go}
							data-to="textColorSelection">
							1 {this.state.textValue}
						</CellButton>
						<CellButton 
							style={{fontFamily: "Carolina Regular",
									backgroundColor: "white"}}
							onClick={this.go}
							data-to="textColorSelection">
							1 {this.state.textValue}
						</CellButton>
						<CellButton 
							style={{fontFamily: "Hitch Hike",
									backgroundColor: "white"}}
							onClick={this.go}
							data-to="textColorSelection">
							1 {this.state.textValue}
						</CellButton>
						<CellButton 
							style={{fontFamily: "JakobC Regular",
									backgroundColor: "white"}}
							onClick={this.go}
							data-to="textColorSelection">
							1 {this.state.textValue}
						</CellButton>
						<CellButton 
							style={{fontFamily: "Lemon Tuesday",
									backgroundColor: "white"}}
							onClick={this.go}
							data-to="textColorSelection">
							1 {this.state.textValue}
						</CellButton>
						<CellButton 
							style={{fontFamily: "Markella Regular",
									backgroundColor: "white"}}
							onClick={this.go}
							data-to="textColorSelection">
							1 {this.state.textValue}
						</CellButton>
						<CellButton 
							style={{fontFamily: "Myriad Pro",
									backgroundColor: "white"}}
							onClick={this.go}
							data-to="textColorSelection">
							1 {this.state.textValue}
						</CellButton>
						<CellButton 
							style={{fontFamily: "Myriad Pro Bold",
									backgroundColor: "white"}}
							onClick={this.go}
							data-to="textColorSelection">
							1 {this.state.textValue}
						</CellButton>
						<CellButton 
							style={{fontFamily: "Teddy Bear Regular",
									backgroundColor: "white"}}
							onClick={this.go}
							data-to="textColorSelection">
							1 {this.state.textValue}
						</CellButton>
						<FixedLayout vertical="bottom">
							<Div style={{display: 'flex'}}>
								<Button 
									className="Button" 
									size="xl"
									after={<img src={onLeft} alt="onLeft"/>}
									stretched 
									level="2" 
									onClick={this.go} 
									data-to="sizeSelection"
								></Button>
								<Button 
									className="Button" 
									size="xl"
									after={<img src={onRight} alt="onRight"/>}
									stretched 
									level="2" 
									onClick={this.go} 
									data-to="textColorSelection"
								></Button>
							</Div>
						</FixedLayout>
					</Group>
			</Panel>
		);
	}
}

const mapStateToProps = (state) => {
	return {
		activePanel: state.nav.activePanel,
		Font: state.font.font,
		text: state.font.text,
	};
};
  
export default connect(mapStateToProps, actions)(FontSelection);