import React from 'react';
import {Panel, PanelHeader, Group, Button, Div, FixedLayout, Slider, Input, FormLayout, Select} from '@vkontakte/vkui';
import { connect } from 'react-redux';
import * as actions from '../Store/actions';
import '../style.css';
import onRight from '../img/onRight.png';
import onLeft from '../img/onLeft.png';

const SizeSelection = ({go, Type, SizeWidth, SizeHight, handleWidth, handleHight}) => {
		return (
			<Panel id={"sizeSelection"} style={{overflow: 'hidden'}}>
				<PanelHeader>
					 <strong></strong>
				</PanelHeader>
				<Group className="Group">
					{Type === "" &&	
						<FormLayout>
							<Div style={{backgroundColor: '#ffffff'}}>
								,  <hr/>
								<Select 
									onChange={e => handleWidth({ SizeWidth: e.target.value })}
									value={String(SizeWidth)}
								>
                    				<options value={15}> 15</options>
									<options value={25}> 25</options> 
									<options value={35}> 35</options> 
									<options value={50}> 50</options> 
              					</Select>
							</Div>
							<Div style={{backgroundColor: '#ffffff'}}>
								,  <hr/>
								<Slider
		            	    	    min={10}
		            	    	    max={30}
		            	    	    value={Number(SizeHight)}
		            	    	    onChange={SizeHight => handleHight({SizeHight})}
		            	    	/>
								<Input 
									value={String(SizeHight)} 
									onChange={e => handleHight({ SizeHight: e.target.value })} 
									type="number"
								/>
							</Div>
					   	</FormLayout>
					}
					<FixedLayout vertical="bottom">
						<Div style={{display: 'flex'}}>
							<Button 
								className="Button" 
								size="xl"
								after={<img src={onLeft} alt="onLeft"/>} 
								stretched 
								level="2" 
								onClick={go} 
								data-to="viewSelection"
							></Button>
							<Button 
								className="Button" 
								size="xl"
								after={<img src={onRight} alt="onRight"/>} 
								stretched 
								level="2" 
								onClick={go} 
								data-to="fontSelection"
							></Button>
						</Div>
					</FixedLayout>
				</Group>
			</Panel>
		)
	}

const mapStateToProps = (state) => {
	return {
		activePanel: state.nav.activePanel,
		Type: state.type.Type,
		SizeWidth: state.size.SizeWidth,
		SizeHight: state.size.SizeHight,
	};
};
  
export default connect(mapStateToProps, actions)(SizeSelection);