import { combineReducers } from 'redux';
import navReducers from '../Store/1Nav/reducers';
import fetchReducers from '../Store/2FetchUser/redusers';
import typeReducers from '../Store/3TypeSelection/reducers';
import materialReducers from '../Store/4MaterialSelection/reducers';
import viewReducers from '../Store/5ViewSelection/reducers';
import sizeReducers from './6SizeSelection/reducers';
import fontReducers from './7FontSelection/reducers';
import colorReducers from './8TextColorSelection/reducers';
import picturesReducers from './9AddingPictures/reducers';
import backgroundColorReducers from './10BackgroundColorSelection/reducers';

export default combineReducers({
    nav: navReducers,
    fetch: fetchReducers,
    type: typeReducers,
    material: materialReducers,
    view: viewReducers,
    size: sizeReducers,
    font: fontReducers,
    text: colorReducers,
    pictures: picturesReducers,
    background: backgroundColorReducers,
});