import React from 'react';
//import connect from '@vkontakte/vkui-connect';
//import connect from '@vkontakte/vkui-connect-promise';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/1Home';
import WelcomeScreen from './panels/2WelcomeScreen';
import TypeSelection from './panels/3TypeSelection';
import MaterialSelection from './panels/4MaterialSelection';
import ViewSelection from './panels/5ViewSelection';
import SizeSelection from './panels/6SizeSelection';
import FontSelection from './panels/7FontSelection';
import TextColorSelection from './panels/8TextColorSelection';
import AddingPictures from './panels/9AddingPictures';
import BackgroundColorSelection from './panels/10BackgroundColorSelection';
import SendLayout from './panels/11SendLayout';
import { connect } from 'react-redux';
import * as actions from '../src/Store/actions';
import  vkConnect from '@vkontakte/vkui-connect';//  

class App extends React.Component {
	componentDidMount() {
		vkConnect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				default:
					console.log(e.detail.type);
			}
		});
		vkConnect.send('VKWebAppGetUserInfo', {});
	}

	render() {
		const { activePanel } = this.props;
		console.log(this.props);
		return (
			<View activePanel={activePanel}>
				<Home id="home" />
				<WelcomeScreen id="welcomeScreen" />
				<TypeSelection id="typeSelection" />
				<MaterialSelection id="materialSelection" />
				<ViewSelection id="viewSelection" />
				<SizeSelection id="sizeSelection" /> 
				<FontSelection id="fontSelection" />
				<TextColorSelection id="textColorSelection" />
				<AddingPictures id="addingPictures" />  
				<BackgroundColorSelection id="backgroundColorSelection" />
				<SendLayout id="sendLayout" />
			</View>
		);
	}
}

const mapStateToProps = (state) => {
	return {
		activePanel: state.nav.activePanel,
		fetchedUser: state.fetch.fetchedUser,
	};
  };
  
export default connect(mapStateToProps, actions)(App);
  