import React from 'react';
import {Panel, PanelHeader, Group, Button, Div, FixedLayout, List, Cell} from '@vkontakte/vkui';
import { connect } from 'react-redux';
import * as actions from '../Store/actions';
import '../style.css';
import { ChromePicker, CirclePicker } from 'react-color';
import onRight from '../img/onRight.png';
import onLeft from '../img/onLeft.png';

//function onChange(obj) {
//	console.log(obj.hex);
//  };
//
const TextColorSelection = ({go, handleTextColor, Material, Type, TextColor}) => {
	return (
		<Panel id={"textColorSelection"}>
			<PanelHeader>
				 <strong> </strong>
			</PanelHeader>
			<Group className="Group">
				{Material === "" &&
					<Div>
						<Div style={{margin: '20px 20px 20px', textAlign: 'center'}}>
							 
							<div style={{ backgroundColor: '#E27974', padding: 20 }}>
								<ChromePicker color={TextColor} onChange={handleTextColor} />
							</div>
							    
							<CirclePicker color={TextColor} colors={["#FFD700", "#c5c9c7"]} onChange={handleTextColor}/>
						</Div>
						<List>
							<Cell multiline>
								<Div 
									style={{backgroundColor: '#ffffff'}}>
									 
									<hr/>
									  ,        .
								</Div>
							</Cell>
						</List>
					</Div>
				}
				{Material === "" &&
					<Div>
						<Div style={{margin: '20px 20px 20px', textAlign: 'center'}}>
							 
							<div style={{ backgroundColor: '#E27974', padding: 20 }}>
								<ChromePicker  color={TextColor} onChange={handleTextColor} />
							</div>
						</Div>
						<List>
							<Cell multiline>
								<Div 
									style={{backgroundColor: '#ffffff'}}>
									 
									<hr/>
									 .    .   .
								</Div>
							</Cell>
						</List>
					</Div>
				}
				{Material === "" &&
					<Div>
						<Div style={{margin: '20px 20px 20px', textAlign: 'center'}}>
							 
							<div style={{ backgroundColor: '#E27974', padding: 20 }}>
								<ChromePicker  color={TextColor} onChange={handleTextColor} />
							</div>
						</Div>
						<List>
							<Cell multiline>
								<Div 
									style={{backgroundColor: '#ffffff'}}>
									 
									<hr/>
									  .    .    20 .
								</Div>
							</Cell>
						</List>
					</Div>
				}
				{Material === "" &&
					<Div>
						<Div style={{margin: '20px 20px 20px', textAlign: 'center'}}>
							    
							<CirclePicker color={TextColor} colors={["#FFD700", "#c5c9c7", "#000000", "#0000FF", "#FF0000", "#008000", "#FFC0CB"]} onChange={handleTextColor}/>
						</Div>
						<List>
							<Cell multiline>
								<Div 
									style={{backgroundColor: '#ffffff'}}>
									 
									<hr/>
									,     .     .
								</Div>
							</Cell>
						</List>
					</Div>
				}
				{Material === "" &&
					<Div>
						<Div style={{margin: '20px 20px 20px', textAlign: 'center'}}>
							 
							<div style={{ backgroundColor: '#E27974', padding: 20 }}>
								<ChromePicker  color={TextColor} onChange={handleTextColor} />
							</div>
						</Div>
						<List>
							<Cell multiline>
								<Div 
									style={{backgroundColor: '#ffffff'}}>
									 
									<hr/>
									    , , ,   .
								</Div>
							</Cell>
						</List>
					</Div>
				}
				{Material === "" &&
					<Div>
						<Div style={{margin: '20px 20px 20px', textAlign: 'center'}}>
							 
							<div style={{ backgroundColor: '#E27974', padding: 20 }}>
								<ChromePicker  color={TextColor} onChange={handleTextColor} />
							</div>
						</Div>
						<List>
							<Cell multiline>
								<Div 
									style={{backgroundColor: '#ffffff'}}>
									 
									<hr/>
									 . 100% .   .    .
								</Div>
							</Cell>
						</List>
					</Div>
				}		
				{Type === " " &&
					<Div>
						<Div style={{margin: '20px 20px 20px', textAlign: 'center'}}>
							 
							<div style={{ backgroundColor: '#E27974', padding: 20 }}>
								<ChromePicker  color={TextColor} onChange={handleTextColor} />
							</div>
						</Div>
						<List>
							<Cell multiline>
								{Material === "" && <Div style={{backgroundColor: '#ffffff'}} > <hr/>   .</Div>}
       	    	            	{Material === "-" && <Div style={{backgroundColor: '#ffffff'}} >- <hr/>    , , ,   .</Div>}
							</Cell>
						</List>
					</Div>
				}	
				{Type === "" &&
					<Div>
						<Div style={{margin: '20px 20px 20px', textAlign: 'center'}}>
							
							 

							<div style={{ backgroundColor: '#E27974', padding: 20 }}>
								<CirclePicker 
									color={TextColor} 
									colors={["#FFD700", "#c5c9c7", "#000000", "#0000FF"]} 
									onChange={handleTextColor}
								/>
								<hr/>
								<CirclePicker 
									color={TextColor} 
									colors={["#FF0000", "#008000", "#FFC0CB"]} 
									onChange={handleTextColor}
								/>
							</div>
						</Div>
						<List>
							<Cell multiline>
								<Div 
									style={{backgroundColor: '#ffffff'}}>
									 
									<hr/>
									 /  /  /  /  /  / 
								</Div>
							</Cell>
						</List>
					</Div>
				}
				{Type === "" &&
					<Div>
						<Div style={{margin: '20px 20px 20px', textAlign: 'center'}}>
							 
							<div style={{ backgroundColor: '#E27974', padding: 20 }}>
								<ChromePicker  color={TextColor} onChange={handleTextColor} />
							</div>
						</Div>
						<List>
							<Cell multiline>
								<Div 
									style={{backgroundColor: '#ffffff'}}>
									 
									<hr/>
									   .
								</Div>
							</Cell>
						</List>
					</Div>
				}			
				<FixedLayout vertical="bottom">
					<Div style={{display: 'flex'}}>
						<Button 
							className="Button" 
							size="xl"
							after={<img src={onLeft} alt="onLeft"/>}
							stretched 
							level="2" 
							onClick={go} 
							data-to="fontSelection"
						></Button>
						<Button 
							className="Button" 
							size="xl"
							after={<img src={onRight} alt="onRight"/>} 
							stretched 
							level="2" 
							onClick={go} 
							data-to="addingPictures"
						></Button>
					</Div>
				</FixedLayout>
			</Group>
		</Panel>
	)
}

const mapStateToProps = (state) => {
	return {
		activePanel: state.nav.activePanel,
		TextColor: state.text.TextColor,
		Material: state.material.Material,
		Type: state.type.Type,
	};
};
  
export default connect(mapStateToProps, actions)(TextColorSelection);