import React from 'react';
import {Panel, PanelHeader, Group, Button, Div, FixedLayout} from '@vkontakte/vkui';
import { connect } from 'react-redux';
import * as actions from '../Store/actions';
import '../style.css';
import onRight from '../img/onRight.png';
import onLeft from '../img/onLeft.png';

const WelcomeScreen = ({ go, fetchedUser, fetchUser }) => {
		return(
			<Panel id={"welcomeScreen"} style={{overflow: 'hidden'}}>
				<PanelHeader>
					 
				</PanelHeader>
				<Group className="Group">
					<Div style={{ textAlign: "center"}}>. 
						{fetchUser}
						<p>        .     , 
    		        	 ,     .</p>
					</Div>
					<FixedLayout vertical="bottom">
						<Div style={{display: 'flex'}}>
							<Button 
								className="Button"
								size="xl" 
								after={<img src={onLeft} alt="onLeft"/>} 
								stretched level="2" 
								onClick={go} 
								data-to="home"
								>
							</Button>
							<Button 
								className="Button"
								size="xl" 
								after={<img src={onRight} alt="onRight"/>} 
								stretched level="2" 
								onClick={go} 
								data-to="typeSelection"
								>
							</Button>
						</Div>	
    		  	    </FixedLayout>
				</Group>
			</Panel>
		);
	}

const mapStateToProps = (state) => {
	return {
		activePanel: state.nav.activePanel,
		fetchedUser: state.fetch.fetchedUser,
	};
};
  
export default connect(mapStateToProps, actions)(WelcomeScreen);
