import React from 'react';
import {Panel, PanelHeader, Group, Button, Div, FixedLayout, List, Cell} from '@vkontakte/vkui';
import { connect } from 'react-redux';
import * as actions from '../Store/actions';
import '../style.css';

import Nastrochnaya from '../img/formButtons/Nadstrochnaya.png';
import Flag from '../img/formButtons/Flag.png';
import Loop from '../img/formButtons/loop.png';
import onRight from '../img/onRight.png';
import onLeft from '../img/onLeft.png';
import rectangular from '../img/formButtons/1rectangular.png';
import roundedEdges from '../img/formButtons/2roundedEdges.png';
import roundTop from '../img/formButtons/3roundTop.png';
import houseRoof from '../img/formButtons/4houseRoof.png';
import circle from '../img/formButtons/5circle.png'

const ViewSelection = ({go, handleView, Type, View}) => {
		return(
			<Panel id={"viewSelection"} style={{overflow: 'hidden'}}>
				<PanelHeader>
					 <strong></strong>
				</PanelHeader>
				<Group className="Group">
					{Type === "" &&
						<Div>
							<Div>
								<Button className={View === "Nastrochnaya"?
									"logoButton" : "Button"} 
									size="xl"
									stretched
									level="2" 
									onClick={() => handleView("Nastrochnaya")}
								></Button>
								<Button className={View === "Flag"?
									"logoButton" : "Button"} 
									size="xl"
									stretched
									level="2"
									onClick={() => handleView("Flag")}
								></Button>
								<Button className={View === "Loop"?
									"logoButton" : "Button"} 
									size="xl"
									stretched
									level="2"
									onClick={() => handleView("Loop")}
								></Button>
							</Div>
							<List>
								<Cell multiline>
									{View === "Nastrochnaya" && <img src={Nastrochnaya} alt="Nastrochnaya"/>}
									{View === "Flag" && <img src={Flag} alt="Flag"/>}
									{View === "Loop" && <img src={Loop} alt="loop"/>}
								</Cell>
							</List>
						</Div>
					}
					{Type === " " && 
						<Div>
							<Div>
								<Button className={View === "rectangular"?
									"logoButton" : "Button"} 
									size="xl"
									stretched
									after={<img src={rectangular} alt="onLeft"/>}
									level="2" 
									onClick={() => handleView("rectangular")}
								></Button>
								<Button className={View === "roundedEdges"?
									"logoButton" : "Button"} 
									size="xl"
									stretched
									after={<img src={roundedEdges} alt="onLeft"/>}
									level="2"
									onClick={() => handleView("roundedEdges")}
								></Button>
								<Button className={View === "roundTop"?
									"logoButton" : "Button"} 
									size="xl"
									stretched
									after={<img src={roundTop} alt="onLeft"/>}
									level="2"
									onClick={() => handleView("roundTop")}
								></Button>
								<Button className={View === "houseRoof"?
									"logoButton" : "Button"} 
									size="xl"
									stretched
									after={<img src={houseRoof} alt="onLeft"/>}
									level="2" 
									onClick={() => handleView("houseRoof")}
								></Button>
								<Button className={View === "his"?
									"logoButton" : "Button"} 
									size="xl"
									stretched
									level="2"
									onClick={() => handleView("his")}
								></Button>
								<Button className={View === "circle"?
									"logoButton" : "Button"} 
									size="xl"
									stretched
									after={<img src={circle} alt="onLeft"/>}
									level="2"
									onClick={() => handleView("circle")}
								></Button>
							</Div>
							<List>
								<Cell multiline>
									{View === "rectangular" && <Div style={{backgroundColor: '#ffffff'}} > <hr/>....</Div>}
									{View === "roundedEdges" && <Div style={{backgroundColor: '#ffffff'}} >   <hr/>....</Div>}
									{View === "roundTop" && <Div style={{backgroundColor: '#ffffff'}} >  <hr/>....</Div>}
									{View === "houseRoof" && <Div style={{backgroundColor: '#ffffff'}} >  <hr/>....</Div>}
									{View === "his" && <Div style={{backgroundColor: '#ffffff'}} >  <hr/>....</Div>}
									{View === "circle" && <Div style={{backgroundColor: '#ffffff'}} > <hr/>....</Div>}
								</Cell>
							</List>
						</Div>
					}
                    {Type === "" && 
						<Div style={{backgroundColor: '#ffffff'}} > <hr/>    . <hr/>    </Div>
					}
					{Type === "" && 
						<Div>
							<Div>
								<Button className={View === "rectangular"?
									"logoButton" : "Button"} 
									size="xl"
									stretched
									after={<img src={rectangular} alt="onLeft"/>}
									level="2" 
									onClick={() => handleView("rectangular")}
								></Button>
								<Button className={View === "roundedEdges"?
									"logoButton" : "Button"} 
									size="xl"
									stretched
									after={<img src={roundedEdges} alt="onLeft"/>}
									level="2"
									onClick={() => handleView("roundedEdges")}
								></Button>
								<Button className={View === "circle"?
									"logoButton" : "Button"} 
									size="xl"
									stretched
									after={<img src={circle} alt="onLeft"/>}
									level="2"
									onClick={() => handleView("circle")}
								></Button>
								<Button className={View === "his"?
									"logoButton" : "Button"} 
									size="xl"
									stretched
									level="2"
									onClick={() => handleView("his")}
								></Button>
							</Div>
							<List>
								<Cell multiline>
									{View === "rectangular" && <Div style={{backgroundColor: '#ffffff'}} > <hr/>....</Div>}
									{View === "roundedEdges" && <Div style={{backgroundColor: '#ffffff'}} >   <hr/>....</Div>}
									{View === "his" && <Div style={{backgroundColor: '#ffffff'}} >  <hr/>....</Div>}
									{View === "circle" && <Div style={{backgroundColor: '#ffffff'}} > <hr/>....</Div>}
								</Cell>
							</List>
						</Div>
					}
					<FixedLayout vertical="bottom">
						<Div style={{display: 'flex'}}>
							<Button 
								className="Button" 
								size="xl"
								after={<img src={onLeft} alt="onLeft"/>}
								stretched 
								level="2" 
								onClick={go} 
								data-to="materialSelection"
							></Button>
							<Button 
								className="Button" 
								size="xl"
								after={<img src={onRight} alt="onRight"/>}
								stretched 
								level="2" 
								onClick={go} 
								data-to="sizeSelection"
							></Button>
						</Div>
					</FixedLayout>
				</Group>
			</Panel>
		);
	}

const mapStateToProps = (state) => {
	return {
		activePanel: state.nav.activePanel,
		Type: state.type.Type,
		View: state.view.View,
	};
};
  
export default connect(mapStateToProps, actions)(ViewSelection);